* {
  padding: 0;
  margin: 0;
}
.mk {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-color: greenyellow;
  justify-content: center;
}
.p {
  font-size: 2em;
  padding-bottom: 1.7em;
}
.d {
  padding: 25px 100px;
  border-radius: 25px;
  margin-left: 210px;
}
.loll {
  font-size: 1.2em;
  text-decoration: none;
  display: inline-block;
  background-color: white;
  border: solid 2px black;
  border-radius: 25px;
  padding: 22px 25px;
}
.loll:hover {
  color: white;
  background-color: rgb(94, 94, 232);
}
.k {
  background: url(img/th5.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  width: 100%;
  min-height: 100vh;
}
@media (max-width: 1023px) {
  .p {
    text-align: center;
    font-size: 1.2em;
  }
  .d {
    text-align: center;
    padding: 15px 20px;
    border-radius: 25px;
    margin-left: 3px;
  }
  .jj {
    text-align: center;
  }
  input {
    text-align: center;
  }
}
